.site__intro {

    @include breakpoint(laptop) {
        font-size: 2rem;
        padding: 2em;

        padding: 5vw 20vw;
    }

    h1,
    h2,
    h3,
    p {
        color: #fff;
        font-size: inherit;
        text-align: center;
    }

    h1 {
        font-size: 3em;

        @include breakpoint(laptop) {
            font-size: 4em;

        }
    }


}



.home {

    .map__link,
    .search-by-zip {

        padding: 2em;

        @include breakpoint(laptop) {

            padding: 1vw;
            justify-content: center;
        }

        h2 {
            color: #fff;
            font-size: 1.5em;

            @include breakpoint(laptop) {
                font-size: 3em;
                margin: 1em;
                margin-bottom: auto;

            }
        }
    }


    #searchBarZip{
        margin-bottom: auto;
    }

}